import { ExitToApp } from '@material-ui/icons'
import { useReaction } from '@speedlo/hooks'
import { observer } from 'mobx-react-lite'
import React from 'react'
import { Link } from 'react-router-dom'
import styled from 'styled-components/macro'

import { mediaDesktop, mediaMobile } from '../app/Responsive'
import { useRoot } from '../atoms/RootProvider'
import { QLogoutWidgetUser, useQLogoutWidget } from '../graph/generated'
import { useLoadingCache } from '../hooks/useLoadingCache'
import { i18n } from '../i18n'
import { LiveClock } from '../molecules/LiveClock'
import { ROUTE_AUTH_LOGOUT } from '../segments/login/login.routes'
import { Colors, Spacing } from '../styling'

const StyledLogoutWidget = styled.div`
  color: ${Colors.greyLight};
  ${mediaDesktop`
    margin-left: ${Spacing.Middle};
  `};
  ${mediaMobile`
    margin-left: ${Spacing.Small};
  `};
  &:hover {
    color: ${Colors.white};
  }
  & a {
    white-space: nowrap;
  }
  & span.name {
    color: ${Colors.greyLight};
    text-decoration: none;
    font-weight: lighter;
    position: relative;
    display: inline-block;
    max-width: 10rem;
    text-align: right;
    ${mediaDesktop`
    top: 0.6rem;
    `}
    ${mediaMobile`
       top: 0.3rem;
    `}
  }

  & svg {
    color: ${Colors.greyLight};
    vertical-align: top;
    margin: 0.4rem;
    ${mediaMobile`
       margin: 0.2rem;
    `}
  }

  & div.clock {
    font-size: 1.3rem;
  }
`

export const LogoutWidget = observer(() => {
  const { auth } = useRoot()
  const { data, loading, refetch } = useQLogoutWidget(
    { id: auth.userId! },
    { skip: !auth.isAuthenticated, fetchPolicy: 'cache-first' },
  )

  useReaction(
    () => auth.userId,
    userId => {
      if (userId !== null) {
        refetch({ id: userId })
      }
    },
  )

  const getUser = useLoadingCache<QLogoutWidgetUser>(!auth.isAuthenticated)

  const user = getUser(loading, () => data.user)

  const displayName =
    user === null
      ? i18n.t`Not logged in`
      : `${data.user.firstName} ${data.user.lastName}`

  return (
    <StyledLogoutWidget data-testid="logout-widget">
      <Link to={ROUTE_AUTH_LOGOUT}>
        <span className="name">{displayName}</span>
        <ExitToApp />
      </Link>
      <LiveClock />
    </StyledLogoutWidget>
  )
})
