import { RM } from '@speedlo/tools'

export function isPartialMatch(searchValue: string) {
  if (!searchValue) {
    return RM.T
  }
  const search = searchValue.toLocaleLowerCase()
  return (propValue: string) => {
    if (!propValue) {
      return true
    }
    return propValue.toLocaleLowerCase().includes(search)
  }
}
