import debug from 'debug'
import React from 'react'
import { Redirect, Route } from 'react-router-dom'

import { ROUTE_AUTH_LOGIN } from '../segments/login/login.routes'

interface IRedirectToLoginProps {
  useOrigin?: boolean
}

const log = debug('routing')

export const RedirectToLogin: React.FC<IRedirectToLoginProps> = ({
  useOrigin = true,
}) => (
  <Route
    path={ROUTE_AUTH_LOGIN}
    children={({ match, location }) => {
      if (match) {
        return null
      }
      log('redirecting to login...')
      return (
        <Redirect
          to={{
            pathname: ROUTE_AUTH_LOGIN,
            state: { origin: useOrigin ? location.pathname : '/' },
          }}
        />
      )
    }}
  />
)
