import { Sentry } from '@speedlo/sentry'
import { intercept } from 'mobx'
import { observer } from 'mobx-react-lite'
import React from 'react'

import { useRoot } from '../atoms/RootProvider'
import { useQUserBootstrap } from '../graph/generated'
import { RM } from '../helpers/ramda'

export const UserBootstrap = observer(() => {
  const { auth, user } = useRoot()

  const { data, loading } = useQUserBootstrap(
    { userId: auth.userId! },
    { skip: !auth.isAuthenticatedUser },
  )

  // UserModel needs to be reset whenever auth user changes
  React.useEffect(() =>
    intercept(auth, 'userId', change => {
      user.invalidate()
      return change
    }),
  )

  // un-authenticated user
  React.useEffect(() => {
    if (user.isLoaded) {
      return
    }

    if (!auth.userId) {
      user.finishBootstrap()
      Sentry.configureScope(scope => {
        scope.setUser({
          id: undefined,
          username: undefined,
        })
        scope.setExtra('roles', [])
        scope.setExtra('branches', [])
      })
    }

    if (auth.isAuthenticatedUser && !loading) {
      // FIXME: Should be removed when BE drops language caching
      if (user.language === null) {
        user.setLanguage(data.user.language.rawCode)
      }
      user.applyUserData(data.user)
      user.applyAccess(data.access)
      user.applyPermissions(data.permissions)
      user.finishBootstrap()
      Sentry.configureScope(scope => {
        scope.setUser({
          id: auth.userId!,
          username: `${data.user.firstName} ${data.user.lastName}`,
        })
        scope.setExtras({
          roles: data.user.roles.map(RM.prop('name')),
          branches: data.user.branches.map(RM.prop('id')),
        })
      })
    }
  })

  return null
})
