import { SECOND, useTimer } from '@speedlo/hooks'
import { DF } from '@speedlo/tools'
import React from 'react'

interface IProps {}

export const LiveClock: React.FC<IProps> = props => {
  const now = useTimer({ enabled: true, interval: SECOND })
  return <div className="clock">{DF.format(now, 'H:mm:ss')}</div>
}
